import React, { useState } from "react";
import "./SearchPhotos.css";

function SearchPhotos() {
  const [value, setValue] = useState("");

  const fetchImages = () => {
    fetch(
      `https://api.unsplash.com/search/photos?client_id=jvSl80F5l12R9uWksb_Fd78gA84uqL9RqnVSB3viIFo&query=${value}`
    )
      .then((res) => res.json())
      .then((data) => console.log(data));
  };

  const handleSeachInputKeyPress = event => {
    if (event.key === 'Enter') {
      fetchImages()
    }
  }

  return (
    <div className="search-container" onClick={fetchImages}>
      <img
        src="https://img.icons8.com/ios-glyphs/20/000000/search.png"
        alt=""
        className="search-icon"
      ></img>
      <input
        type="text"
        name="query"
        className="search-input"
        placeholder="Search free high-resolution photos"
        value={value}
        onKeyPress={handleSeachInputKeyPress}
        onChange={(e) => setValue(e.target.value)}
      />
    </div>
  );
}

export default SearchPhotos;
